/* Общий фон */
.nft-banner {
  position: relative;
  background-color: #1a1a1a;
  border-radius: 2vw;
  overflow: hidden;
  padding: 3vw 0;
  margin: 0 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  box-shadow: 0 0.5vw 2vw rgba(0, 0, 0, 0.5);
}

/* Карточки NFT */
.nft-cards {
  display: flex;
  gap: 2vw;
  overflow: hidden;
  position: absolute;
  inset: 0;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.nft-card {
  width: 10vw;
  height: 14vw;
  background: url('./assets//images//card.png') no-repeat center center;
  background-size: cover;
  border: 0.1vw solid orange;
  border-radius: 0.6vw;
  filter: blur(0.3vw);
  opacity: 0.9;
}


/* Контент баннера */
.nft-banner-content {
  position: relative;
  z-index: 2;
  text-align: center;
  color: white;
  margin: 0 auto;
  padding: 1.5vw 3vw ;
  background: rgba(80, 80, 80, 0.2); /* Полупрозрачный черный фон */
  border-radius: 2vw; /* Закругленные углы */
  backdrop-filter: blur(0.5vw); /* Размытие фона */
  box-shadow: 0 0.1vw 1.5vw rgba(5, 5, 5, 0.8); /* Тень для акцента */
}

/* Заголовок */
.nft-banner-content h2 {
  font-size: 1.7vw;
  font-weight: bold;
  margin-bottom: 0.5vw;
}

/* Описание */
.nft-banner-content p {
  font-size: 0.7vw;
  opacity: 0.8;
  margin-bottom: 0.5vw;
}

/* Кнопка */
.join-discord {
  background: rgba(131, 131, 131, 0.1); /* Полупрозрачный черный фон */
  color: white;
  border: none;
  border: 0.1vw solid #aaa;
  padding: 0.3vw 1.5vw;
  padding-bottom: 0.1vw;
  border-radius: 2vw;
  cursor: pointer;
  font-size: 1vw;
  display: flex;
  align-items: center;
  gap: 0.8vw;
  display: flex;
  margin: 0 auto;
  transition: transform 0.2s, background-color 0.2s;
  backdrop-filter: blur(0.1vw);
}

.join-discord:hover {
  transform: scale(1.05);
  border: 0.1vw solid #aaa;
}

.discord-icon img{
  width: 1.5vw;
  padding-top: 0.1vw;
}

.join-discord::after {
  content: "";
  position: absolute;
  top: 100%; /* Показывается под кнопкой */
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-size: 0.7vw;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  white-space: nowrap;
  margin-top: 0.4vw;
}

.join-discord:hover::after {
  opacity: 1;
  visibility: visible;
}